@import '../variables';

.sort {
  margin-bottom: $spacing;
  // text-align: right;
  font-weight: 500;

  li {
    display: inline-block;
    margin-right: $spacing;
  }

  a {
    text-decoration: none;
  }
}

.title {
  text-align: center;
  font-weight: 500;
  font-size: 12.5px;
  margin-bottom: 0px;
  a {
    text-decoration: none;
    color: $black
  }

}

.playlist {
  margin-bottom: $spacing;
}

.activeSortLink {
  padding-bottom: 2px;
  border-bottom: 2px $link-color solid;

  &:hover {
    border-bottom-color: lighten($link-color, 20%);
  }
}



